body {
  margin: 0;
  padding: 0;
  background-color: #ffdb8f;
}
main {
  margin: 0;
  padding: 0 10px;
  color: #570966;
  font-family: "Roboto-Mono", "Arial", serif;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.main-header {
  margin-top: -10px;
  padding-top: 0;
  background-color: #ff5754;
  font-family: "Roboto-Mono", "Arial", serif;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #ffdb8f;
  font-size: 10px;
}
.main-header__container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-bottom: 10px;
}
.main-header__header {
  font-family: "Lobster", "Arial", sans-serif;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-stretch: normal;
  line-height: 24px;
  letter-spacing: normal;
  color: #ffdb8f;
}
.main-header__name {
  font-size: 30px;
  display: block;
  text-align: center;
  position: relative;
  z-index: 5;
}
.main-header__logo {
  width: 80px;
  height: 80px;
  margin: -28px auto -18px auto;
  position: relative;
  z-index: 1;
}
.main-header__description {
  font-family: "Roboto-Mono", "Arial", serif;
  font-size: 10px;
  line-height: 16px;
  text-align: center;
  margin: 0;
  padding: 0 35px;
  position: relative;
  z-index: 5;
}
.navigation__container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 0;
  padding: 0;
}
.navigation__logo-inner {
  margin: 0;
  padding: 0;
}
.user-navigation__menu,
.main-navigation__menu {
  margin: 0;
  padding: 0;
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.user-navigation__menu li,
.main-navigation__menu li {
  list-style: none;
  text-decoration: none;
  padding: 0 5px;
}
.user-navigation__link a,
.main-navigation__link a {
  font-size: 10px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffdb8f;
}
.sign-in-form-container {
  margin: 15px auto;
  padding: 15px 15px;
  display: flex;
  flex-direction: column;
  border: 2px solid #570966;
  border-radius: 5px;
}
.sign-in-form {
  margin: 0;
  padding: 0;
}
.sign-in-form__header {
  padding: 0;
  text-align: center;
  margin: 0 0 10px 0;
}
.sign-in-form__label {
  font-family: Roboto-Thin, sans-serif;
  display: block;
  font-size: 11px;
  font-style: italic;
  margin-bottom: 2px;
}
.sign-in-form__email,
.sign-in-form__pass {
  box-sizing: border-box;
  margin-bottom: 5px;
  border: 1px solid #570966;
  background-color: bisque;
  border-radius: 5px;
  width: 100%;
  max-width: 300px;
}
.sign-in-form__help-container {
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}
.sign-in-form__forgot-reg {
  color: #570966;
  font-family: Roboto-Thin, sans-serif;
  display: block;
  font-size: 10px;
  font-style: italic;
  margin-bottom: 2px;
  padding-right: 5px;
}
.sign-in-form__submit {
  background-color: #ff5754;
  padding: 6px 23px;
  border-radius: 14px;
  display: block;
  margin: 0 auto;
  color: #ffdb8f;
  border: none;
}
.main-footer {
  background-color: #ff5754;
  color: #ffdb8f;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.footer__container {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  margin: 10px 15px;
}
.footer-social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100px;
  margin: 10px auto;
}
.footer-social__link {
  text-decoration: none;
  color: #ffdb8f;
  font-family: Roboto-Mono, sans-serif;
  font-size: 15px;
}
.author {
  margin: 10px auto;
}
.author__name {
  text-decoration: none;
  color: #ffdb8f;
  font-family: Roboto-Mono, sans-serif;
  font-size: 11px;
}
.footer-social__link {
  display: block;
  width: 20px;
  height: 20px;
}
